import React, { Component } from 'react';

export default class CartItem extends Component {
	render() {
		const { id, title, img, price, total, count } = this.props.item;
		const { increment, decrement, removePizza } = this.props.value;

		return (
			<div className='row my-1 text-capitalize text-center'>
				<div className='col-10 mx-auto col-lg-2'>
					<img
						src={img}
						style={{ width: '5rem', height: '5rem' }}
						className='img-fluid'
						alt=''
					/>
				</div>
				<div className='col-10 mx-auto col-lg-2 '>
					<span className='d-lg-none'></span> {title}
				</div>
				<div className='col-10 mx-auto col-lg-2 '>
					<strong>
						<span className='d-lg-none'></span> ${price}
					</strong>
				</div>
				<div className='col-10 mx-auto col-lg-2 my-2 my-lg-0 '>
					<div className='d-flex justify-content-center'>
						<div>
							<span
								className='btn btn-black mx-1'
								onClick={() => {
									return decrement(id);
								}}
							>
								-
							</span>
							<span className='btn btn-black mx-1'>{count}</span>
							<span
								className='btn btn-black mx-1'
								onClick={() => {
									return increment(id);
								}}
							>
								+
							</span>
						</div>
					</div>
				</div>
				<div className='col-10 mx-auto col-lg-2 '>
					<div className=' cart-icon' onClick={() => removePizza(id)}>
						<img src={'bin.svg'} width='20px' height='20px' alt=''/>
					</div>
				</div>

				<div className='col-10 mx-auto col-lg-2 '>
					<strong>${total} </strong>
				</div>
			</div>
		);
	}
}
